:root {
  --primary-bg-color: #0a192f;
  --primary-text-color: #ccd6f6;
  --accent-color: #64ffda;
  --content-bg-color: rgba(255, 255, 255, 0.1);
  --skill-bg-color: #00bcd4;
  --font-family: 'Roboto', sans-serif;
  --job-date-color: #8892b0;
  --link-color: #64ffda;
  --job-border-color: #233554;
  --content-padding: 30px 15px;
  --max-content-width: 1200px; /* Increased max content width */
}

body {
  margin: 0;
  font-family: var(--font-family);
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  scroll-behavior: smooth;
  display: flex;
  justify-content: center; /* Center-align content horizontally */
}

/* Custom scrollbar styles */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: var(--primary-bg-color);
}

::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 6px;
  border: 3px solid var(--primary-bg-color);
}

.app {
  display: flex;
  width: 100%;
  max-width: var(--max-content-width);
  height: 100vh;
  overflow: hidden;
  position: relative;

  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);

}



.app::before, .app::after {

  content: '';

  position: fixed;

  top: 0;

  width: var(--side-bar-width);

  height: 100%;

  background-color: var(--accent-color);

  z-index: 100;

  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);

}



.app::before {

  left: 0;

}



.app::after {

  right: 0;
}

.sidebar {
  width: 250px;
  background-color: var(--primary-bg-color);
  position: fixed;
  height: 100%;
  padding: 30px 15px;
  transform: translateX(0);
  transition: transform 0.3s ease;
  z-index: 1000;
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px;
}

.sidebar h1 {
  font-size: 1.8em;
  margin-bottom: 10px;
}

.sidebar p {
  font-size: 0.9em;
  margin-bottom: 15px;
}

.sidebar nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 15px;
}

.sidebar-link {
  color: var(--accent-color);
  text-decoration: none;
  margin: 8px 0;
  cursor: pointer;
  background: none;
  border: none;
  color: inherit;
  font-size: 1.1em;
  text-align: left;
  padding: 5px 0;
  width: 100%;
}

.sidebar-link.active {
  font-weight: bold;
}

.social-icons {
  display: flex;
  gap: 10px;
  margin-top: auto;
}

.social-icons a {
  color: var(--accent-color);
  font-size: 1.4em;
  transition: color 0.3s;
}

.social-icons a:hover {
  color: #fff;
}

.content-wrapper {
  margin-left: 250px;
  width: calc(100% - 250px);
  overflow-y: auto;
  padding: var(--content-padding);
}

.main-content {
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center-align content vertically */
}

.section {
  margin-bottom: 30px;
  width: 100%;
}

.content {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  position: relative;
  padding: 15px;
  background: var(--content-bg-color);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.content:hover {
  transform: translateY(-8px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.section h2 {
  color: var(--accent-color);
  margin-bottom: 15px;
  border-bottom: 2px solid var(--accent-color);
  padding-bottom: 8px;
}

.about-content p {
  font-size: 1.1em;
}

.skills-content {
  font-size: 1em;
  line-height: 1.4em;
}

.skills-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 15px;
}

.skills-category {
  margin-bottom: 15px;
}

.skills-category strong {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.skill-item {
  background-color: var(--skill-bg-color);
  color: var(--primary-bg-color);
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 0.8em;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.skill-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.experience-content .job {
  border-left: 2px solid var(--job-border-color);
  padding-left: 15px;
  margin-bottom: 25px;
}

.experience-content .job-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.experience-content .job-title h3 {
  margin: 0;
  color: var(--primary-text-color);
}

.experience-content .job-title span {
  color: var(--job-date-color);
  font-size: 0.9em;
}

.experience-content .job-details {
  display: flex;
  align-items: center;
}

.experience-content .job-details img,
.projects-content .project img {
  width: 70px; /* Adjust the size of the images */
  height: 70px;
  object-fit: contain;
  background-color: var(--primary-bg-color);
  padding: 8px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-right: 15px;
}

.experience-content ul {
  list-style: none;
  padding: 0;
  margin: 10px 0 0;
}

.experience-content ul li {
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
}

.experience-content ul li:before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--accent-color);
  font-size: 1.2em;
}

.projects-content .project {
  border-left: 2px solid var(--job-border-color);
  padding-left: 15px;
  margin-bottom: 25px;
  display: flex;
  align-items: center;
}

.projects-content .project-details {
  flex: 1;
}

.projects-content .project-details h3 {
  margin: 0;
  color: var(--primary-text-color);
}

.projects-content ul {
  list-style: none;
  padding: 0;
  margin: 10px 0 0;
}

.projects-content ul li {
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
}

.projects-content ul li:before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--accent-color);
  font-size: 1.2em;
}

.contact-content .contact-links {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 15px;
  justify-content: center;
}

.contact-content .contact-link {
  padding: 8px 15px;
  border: 1px solid var(--accent-color);
  border-radius: 5px;
  text-decoration: none;
  color: var(--accent-color);
  transition: background-color 0.3s ease, color 0.3s ease;
  font-size: 1em;
}

.contact-content .contact-link:hover {
  background-color: var(--accent-color);
  color: var(--primary-bg-color);
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.sidebar-toggle {
  display: none;
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1001;
  background: none;
  border: none;
  color: var(--accent-color);
  font-size: 2em;
  cursor: pointer;
}

@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: fixed;
    transform: translateX(-100%);
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .content-wrapper {
    margin-left: 0;
    width: 100%;
    padding: 10px;
  }

  .sidebar-toggle {
    display: block;
  }

  .app.sidebar-open .content-wrapper {
    padding-top: 100px; /* Adjust based on your needs */
  }
}
